  computed: {
    calculatedDueDate: {
      get: function() {
        return this.dueDate || this.now
      },
      set: function(newValue) {
        this.dueDate = newValue
      }
    },

    splitCostRules () {
      const rules = []
      let val1 = this.editedItem.costCenterAmount1.replace(",", ".") * 1
      let val2 = this.editedItem.costCenterAmount2.replace(",", ".") * 1
      let valTot = this.editedItem.originalValue.replace(",", ".") * 1
      const rule =
        val1 + val2 === valTot ||
        "A soma do rateio não corresponde ao Valor Total"
      rules.push(rule)

      return rules
    },
    situation () {
      if (this.editedItem.paidValue.replace(",", ".") * 1 == this.editedItem.originalValue.replace(",", ".") * 1) {
        return 'pago'
      } else if (this.editedItem.paidValue.replace(",", ".") * 1 != 0 && this.editedItem.paidValue.replace(",", ".") * 1 <= this.editedItem.originalValue.replace(",", ".") * 1) {
        return 'pago parcial'
      } else if (this.editedItem.openValue.replace(",", ".") * 1 == 0) {
        if (this.editedItem.dueDate && this.parseDate(this.editedItem.dueDate).replace(/-/g, '') < this.parseDate(this.now).replace(/-/g, '')) {
          return 'vencido'
        } else if (this.editedItem.dueDate && this.parseDate(this.editedItem.dueDate).replace(/-/g, '') == this.parseDate(this.now).replace(/-/g, '')) {
          return 'vence hoje'
        } else {
          return 'em aberto'
        }
      } else {
        return 'em aberto'
      }
    },
    filteredInvoices () {
      if (this.selectedFilterItem == "0") {
        return this.invoices.filter(item => (
          this.parseDate(item.dueDate).replace(/-/g, '') >= this.filterStartDate.replace(/-/g, '') &&
          this.parseDate(item.dueDate).replace(/-/g, '') <= this.filterFinalDate.replace(/-/g, '') &&
          item.situation.includes(this.filterSituation) &&
          item.ID.toLowerCase().includes(this.search.toLowerCase())
        ))
      } else if (this.selectedFilterItem == "1") {
        return this.invoices.filter(item => (
          this.parseDate(item.dueDate).replace(/-/g, '') >= this.filterStartDate.replace(/-/g, '') &&
          this.parseDate(item.dueDate).replace(/-/g, '') <= this.filterFinalDate.replace(/-/g, '') &&
          item.situation.includes(this.filterSituation) &&
          item.dueDate.toLowerCase().includes(this.search.toLowerCase())
        ))
      } else if (this.selectedFilterItem == "2") {
        return this.invoices.filter(item => (
          this.parseDate(item.dueDate).replace(/-/g, '') >= this.filterStartDate.replace(/-/g, '') &&
          this.parseDate(item.dueDate).replace(/-/g, '') <= this.filterFinalDate.replace(/-/g, '') &&
          item.situation.includes(this.filterSituation) &&
          item.description.toLowerCase().includes(this.search.toLowerCase())
        ))
      } else if (this.selectedFilterItem == "3") {
        return this.invoices.filter(item => (
          this.parseDate(item.dueDate).replace(/-/g, '') >= this.filterStartDate.replace(/-/g, '') &&
          this.parseDate(item.dueDate).replace(/-/g, '') <= this.filterFinalDate.replace(/-/g, '') &&
          item.situation.includes(this.filterSituation) &&
          item.originalValue.toLowerCase().includes(this.search.toLowerCase())
        ))
      } else if (this.selectedFilterItem == "4") {
        return this.invoices.filter(item => (
          this.parseDate(item.dueDate).replace(/-/g, '') >= this.filterStartDate.replace(/-/g, '') &&
          this.parseDate(item.dueDate).replace(/-/g, '') <= this.filterFinalDate.replace(/-/g, '') &&
          item.situation.includes(this.filterSituation) &&
          item.openValue.toLowerCase().includes(this.search.toLowerCase())
        ))
      } else if (this.selectedFilterItem == "5") {
        return this.invoices.filter(item => (
          this.parseDate(item.dueDate).replace(/-/g, '') >= this.filterStartDate.replace(/-/g, '') &&
          this.parseDate(item.dueDate).replace(/-/g, '') <= this.filterFinalDate.replace(/-/g, '') &&
          item.situation.includes(this.filterSituation) &&
          item.situation.toLowerCase().includes(this.search.toLowerCase())
        ))
      } else {
        return this.invoices.filter(item => (
          this.parseDate(item.dueDate).replace(/-/g, '') >= this.filterStartDate.replace(/-/g, '') &&
          this.parseDate(item.dueDate).replace(/-/g, '') <= this.filterFinalDate.replace(/-/g, '') &&
          item.situation.includes(this.filterSituation)))
      }
    },
    nextMonth () {
      var date = new Date()
      date.setDate(date.getDate() + this.installmentInterval)
      var day = date.getDate()
      var month = date.getMonth() + 1
      var year = date.getFullYear()
      if (month < 10) {
        month = "0" + month
      }
      if (day < 10) {
        day = "0" + day
      }
      return day + "/" + month + "/" + year
    },

    formattedFilterStartDate () {
      return this.formatDate(this.filterStartDate)
    },
    formattedFilterFinalDate () {
      return this.formatDate(this.filterFinalDate)
    },
    logado () {
      return window.sessionStorage.logado == 'true' ? true : false
    },
    user () {
      return window.sessionStorage.user
    },
    userId () {
      return window.sessionStorage.id
    },
    profile () {
      return window.sessionStorage.profile
    },
    disabledBatchEdit () {
      return this.selected.length > 1 ? false : true
    },
    randomQuote () {
      return Math.floor(Math.random() * this.motivationalQuotes.length)
    },
    formTitle () {
      return this.editedIndex === -1
        ? "Novo Lançamento"
        : `Editar Lançamento - ID ${this.editedItem.ID}`
    },
  },

  watch: {
    filteredInvoices (val) {
      if (val) this.totalAmounts()
    },
    alltime () {
      const startDate = new Date(new Date().getFullYear(), new Date().getMonth(), 1).toISOString().substr(0, 10) // first day of the month
      const finalDate = (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10) // today
      if (this.alltime) {
        this.filterStartDate = "1500-04-22"
        this.filterFinalDate = "2099-12-31"
      } else if (!this.alltime) {
        this.filterStartDate = startDate
        this.filterFinalDate = finalDate
      }
    },
    paid (val) {
      if (val && !this.editedItem.paymentDate) this.editedItem.paymentDate = this.now
      if (!val) {
        this.editedItem.paidValue = ''
        this.editedItem.openValue = this.editedItem.originalValue
        this.editedItem.paymentDate = ''
      }
    },
    splitCost (val) {
      if (!val) {
        this.editedItem.costCenter2 = ''
        this.editedItem.costCenterAmount2 = ''
        this.editedItem.costCenterAmount1 = this.editedItem.originalValue
      }
    },
    batchDialog(val) {
      val || this.closeBatch()
    },
    "editedItem.originalValue": {
      handler (val) {
        if (!this.splitCost) this.editedItem.costCenterAmount1 = val
      },
      deep: true,
    },
    "editedItem.paidValue": {
      handler () {
        if (this.editedItem.paidValue) {
          var r = this.editedItem.originalValue.replace(".", "").replace(",", ".") - this.editedItem.paidValue.replace(",", ".")
          r = r.toString()

          if (r.includes(".")) {
            this.editedItem.openValue = r.toString().replace(".", ",")
          } else {
            this.editedItem.openValue = r
          }
        } else {
          this.editedItem.openValue = ""
        }
      },
      deep: true,
    },
    "editedItem.supplierId": {
      handler () {
        if (this.supplierExists === false) {
          this.editedItem.supplierId.length <= 14
            ? (this.supplierIdMask = "###.###.###-##")
            : (this.supplierIdMask = "##.###.###/####-##")
        }
      },
      deep: true,
    },
    "editedItem.installment": {
      handler () {
        if (this.editedItem.installment === 0) {
          this.editedItem.expectedDate = this.now
        } else if (this.editedItem.installment > 0) {
          this.editedItem.expectedDate = this.nextMonth
          this.editedItem.dueDate = this.editedItem.expectedDate
        } else if (!this.editedItem.installment) {
          this.editedItem.expectedDate = ""
        }
      }
    },
    dialog(val) {
      val || this.close()
      if (this.editedIndex === -1) {
        (this.isDisabled = false),
        (this.showCloneBtn = false)
      }
    },
    dialogSave(val) {
      val || this.closeDialogSave()
    },
    dialogDelete(val) {
      val || this.closeDelete()
    },

    tempFirstMaturity () {
      if (this.editedItem.dueDate !== "N/A") {
        this.editedItem.dueDate = this.formatDate(this.tempFirstMaturity)
      }
    },
    tempExpectedDate () {
      this.editedItem.expectedDate = this.formatDate(this.tempExpectedDate)
    },
    tempCompetencyDate () {
      if (this.editedItem.competencyDate !== "N/A") {
        this.editedItem.competencyDate = this.formatDate(this.tempCompetencyDate)
      }
    },
    tempPaymentDate () {
      if (this.editedItem.competencyDate !== "N/A") {
        this.editedItem.competencyDate = this.formatDate(this.tempCompetencyDate)
      }
    },
    err () {
      console.log(this.err)
      alert(
        "Ops! Um erro foi encontrado. Por favor, entre em contato com o suporte."
      )
    },
  },

  created () {
    this.fetchInvoices()
    this.setTimeNow()
    Vue.config.warnHandler = (err) => (this.err = err)
  },

  methods: {
    currencyFormatter (el) {
      return el.toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })
    },
    searchAction () {
      this.searchLoading = true
      setTimeout(() => {
        this.search = this.searchField
        this.searchLoading = false
      }, 300)
    },
    setTimeNow () {
      var date = new Date()
      var day = date.getDate()
      var month = date.getMonth() + 1
      var year = date.getFullYear()
      if (month < 10) {
        month = "0" + month
      }
      if (day < 10) {
        day = "0" + day
      }
      this.now = day + "/" + month + "/" + year
      const m = [
        "JANEIRO",
        "FEVEREIRO",
        "MARÇO",
        "ABRIL",
        "MAIO",
        "JUNHO",
        "JULHO",
        "AGOSTO",
        "SETEMBRO",
        "OUTUBRO",
        "NOVEMBRO",
        "DEZEMBRO",
      ]
      this.monthInWords = m[date.getMonth()]
      if (this.editedIndex === -1) {
        this.editedItem.competencyDate = this.now
      }
    },

    setEditTime () {
      const timeElapsed = Date.now()
      const rightNow = new Date(timeElapsed)
      this.timeNow = rightNow.toLocaleString("pt-BR")
    },

    formatDate (date) {
      if (!date) return null
      var [year, month, day] = date.split('-')
      return `${day.padStart(2, '0')}/${month.padStart(2, '0')}/${year}`
    },
    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    usDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      var secs = '00:00:28'
      return `${month.padStart(2, '0')}/${day.padStart(2, '0')}/${year} ${secs}`
    },

    fetchInvoices () {
      this.fetchingInvoices = true
      return axios.get('https://sheetdb.io/api/v1/58i1u97yptnfw').then((r) => {
        this.invoices = r.data
      }).then(() => {
        this.fetchingInvoices = false
      })
    },
    totalAmounts () {
      var totals = [
        { sum: 0, items: [], situation: 'pago' },
        { sum: 0, items: [], situation: 'vencido' },
        { sum: 0, items: [], situation: 'vence hoje' },
        { sum: 0, items: [], situation: 'em aberto' },
        { sum: 0, items: [], situation: 'pago parcial' },
      ]

      for (var i=0; i < totals.length; i++) {
        totals[i].items = this.filteredInvoices.filter(item => item.situation.includes(totals[i].situation))
        for (var j=0; j<totals[i].items.length; j++) {
          let subTotal = totals[i].items[j].originalValue
          if (subTotal == 'Nan') subTotal = 0
          let formattedNumber = Number(subTotal.replace(",", "."))
          totals[i].sum += formattedNumber
        }
      }

      let subTotalPaid = totals.filter(el => el.situation === 'pago')[0].sum
      let subTotalOverdue = totals.filter(el => el.situation === 'vencido')[0].sum
      let subTotalExpiresToday = totals.filter(el => el.situation === 'vence hoje')[0].sum
      let subTotalDue = totals.filter(el => el.situation === 'em aberto')[0].sum

      this.totalPaid = this.currencyFormatter(subTotalPaid)
      this.totalOverdue = this.currencyFormatter(subTotalOverdue)
      this.totalExpiresToday = this.currencyFormatter(subTotalExpiresToday)
      this.totalDue = this.currencyFormatter(subTotalDue)
    },

    editItem(item) {
      this.err.includes("Duplicado")
        ? alert("Não atualizar lançamentos e contatar suporte.")
        : ((this.editedIndex = this.invoices.indexOf(item)),
          (this.editedItem = Object.assign({}, item)),
          ((this.situation === 'pago' || this.situation === 'pago parcial') ? this.paid = true : this.paid = false),
          ((this.editedItem.costCenter2 || this.editedItem.costCenterAmount2) ? this.splitCost = true : this.splitCost = false),
          (this.dialog = true))
    },
    deleteItem(item) {
      this.editedIndex = this.invoices.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    editItemConfirm () {
      // save edit
      if (this.editedIndex > -1) {
        axios
          .put(
            `https://sheetdb.io/api/v1/58i1u97yptnfw/ID/${this.editedItem.ID}`,
            {
              data: {
                situation: `${this.situation}`,
                supplierId: `${this.editedItem.supplierId}`,
                supplierName: `${this.editedItem.supplierName}`,
                competencyDate: `${this.editedItem.competencyDate}`,
                dueDate: `${this.editedItem.dueDate}`,
                expectedDate: `${this.editedItem.expectedDate}`,
                installment: `${this.editedItem.installment}`,
                description: `${this.editedItem.description}`,
                originalValue: `${this.editedItem.originalValue}`,
                paidValue: `${this.editedItem.paidValue}`,
                openValue: `${this.editedItem.openValue}`,
                bankAccount: `${this.editedItem.bankAccount}`,
                paymentDate: `${this.editedItem.paymentDate}`,
                observation: `${this.editedItem.observation}`,
                category: `${this.editedItem.category}`,
                costCenter1: `${this.editedItem.costCenter1}`,
                costCenterAmount1: `${this.editedItem.costCenterAmount1}`,
                costCenter2: `${this.editedItem.costCenter2}`,
                costCenterAmount2: `${this.editedItem.costCenterAmount2}`,
                edits: `${this.editedItem.edits}` + ` ${this.user} ${this.timeNow}.`,
              },
            }
          )
          .then((response) => {
            if (response.status === 200 || response.status === 201) {
              this.showUpdate = !this.showUpdate
              this.invoicesLoading = !this.invoicesLoading
              this.fetchInvoices()
              this.$nextTick(() => {
                this.invoicesLoading = !this.invoicesLoading
              })
              setTimeout(() => {
                this.showUpdate = !this.showUpdate
              }, 2500)
            }
          })
          .catch((error) => {
            if (error.response) {
              this.showError = !this.showError
              setTimeout(() => {
                this.showError = !this.showError
                window.location.reload()
              }, 1500)
            }
          })
        this.dialogSave = false
        setTimeout(() => {
          this.close()
        }, 200)
      } else {
        this.save()
      }
    },
    deleteItemConfirm () {
      this.invoices.splice(this.editedIndex, 1)
      this.closeDelete()
    },

    close () {
      this.executed = false
      this.dialog = false
      this.supplierExists = true
      this.splitCost = false
      this.paid = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
        this.saving = false
      })
    },
    closeDialogSave () {
      this.dialogSave = false
      this.supplierExists = true
      this.saving = false
    },
    closeDelete () {
      this.dialogDelete = false
      this.supplierExists = true
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
        this.saving = false
      })
    },

    validateField () {
      this.$refs.form.validate()
    },

    clone () {
      if (this.editedIndex > -1) {
        var tempSupplierId = this.editedItem.supplierId
        var tempSupplierName = this.editedItem.supplierName
        var tempCompetencyDate = this.editedItem.competencyDate
        var tempFirstMaturity = this.editedItem.dueDate
        var tempExpectedDate = this.editedItem.expectedDate
        var tempInstallment = this.editedItem.installment
        var tempDescription = this.editedItem.description
        var tempOriginalValue = this.editedItem.originalValue
        var tempPaidValue = this.editedItem.paidValue
        var tempOpenValue = this.editedItem.openValue
        var tempBankAccount = this.editedItem.bankAccount
        var tempPaymentDate = this.editedItem.paymentDate
        var tempObservation = this.editedItem.observation
        var tempCategory = this.editedItem.category
        var tempCostCenter1 = this.editedItem.costCenter1
        var tempCostCenterAmount1 = this.editedItem.costCenterAmount1
        var tempCostCenter2 = this.editedItem.costCenter2
        var tempCostCenterAmount2 = this.editedItem.costCenterAmount2
        setTimeout(() => {
          this.close()
        }, 300)
        setTimeout(() => {
          document.getElementById("newItemBtn").click()
        }, 300)
        setTimeout(() => {
          this.editedItem.supplierId = tempSupplierId
          this.editedItem.supplierName = tempSupplierName
          this.editedItem.competencyDate = tempCompetencyDate
          this.editedItem.dueDate = tempFirstMaturity
          this.editedItem.expectedDate = tempExpectedDate
          this.editedItem.installment = tempInstallment
          this.editedItem.description = tempDescription
          this.editedItem.originalValue = tempOriginalValue
          this.editedItem.paidValue = tempPaidValue
          this.editedItem.openValue = tempOpenValue
          this.editedItem.bankAccount = tempBankAccount
          this.editedItem.paymentDate = tempPaymentDate
          this.editedItem.observation = tempObservation
          this.editedItem.category = tempCategory
          this.editedItem.costCenter1 = tempCostCenter1
          this.editedItem.costCenterAmount1 = tempCostCenterAmount1
          this.editedItem.costCenter2 = tempCostCenter2
          this.editedItem.costCenterAmount2 = tempCostCenterAmount2
        }, 300)
      } else {
        this.showCloneBtn = false
      }
    },

    save () {
      this.saving = true
      var executed = false
      var form = document.getElementById("sheetdb-form")
      if (this.editedIndex > -1) {
        this.dialogSave = true
      } else {
        // salva novo
        if (this.editedIndex === -1) this.saveSupplier()
        if (!this.editedItem.ID)
          alert(
            "lançamento sem ID. Favor contatar suporte antes de prosseguir."
          )
        this.$nextTick(() => {
          if (!executed) {
            executed = true
            fetch(form.action, {
              method: "POST",
              body: new FormData(form),
            })
              .then((response) => {
                if (response.status === 200 || response.status === 201) {
                  this.close()
                  this.showPost = !this.showPost
                  this.fetchInvoices()
                  setTimeout(() => {
                    this.showPost = !this.showPost
                  }, 1500)
                } else if (response.status === 429) {
                  alert(
                    "Erro ao Salvar" +
                      "O limite de solicitações foi excedido! 10.000 licenças foram lançadas esse mês! Entre em contato com o suporte para liberar mais espaço."
                  )
                } else {
                  alert(
                    "Erro ao Salvar" +
                      "Erro de servidor. Entre em contato com o suporte."
                  )
                }
              })
              .catch((error) => {
                if (error.response) {
                  this.showError = !this.showError
                  setTimeout(() => {
                    this.showError = !this.showError
                    window.location.reload()
                  }, 1500)
                }
              })
          }
        })
      }
    },
    saveBatch () {
      this.saving = true
      var executed = false
      if (this.editedIndex > -1) {
        this.dialogSave = true
      } else {
        // save new
        if (this.editedIndex === -1) this.saveSupplier()
        if (!this.editedItem.ID) alert("lançamento sem ID. Favor contatar suporte antes de prosseguir.")
        this.$nextTick(() => {
          if (!executed) {
            executed = true
            for (let i=0; i<this.editedItem.installment*1; i++) {
              axios.post(
                `https://sheetdb.io/api/v1/58i1u97yptnfw`,
                {
                  data: {
                    ID: `${this.editedItem.ID*1 + i}`,
                    situation: `${this.situation}`,
                    supplierId: `${this.editedItem.supplierId}`,
                    supplierName: `${this.editedItem.supplierName}`,
                    competencyDate: `${this.editedItem.competencyDate}`,
                    dueDate: `${this.editedItem.dueDate}`,
                    expectedDate: `${this.editedItem.expectedDate}`,
                    installment: (i+1) + '/' + `${this.editedItem.installment}`,
                    description: `${this.editedItem.description}`,
                    originalValue: `${(this.editedItem.originalValue/this.editedItem.installment).toString().replace('.', ',')}`,
                    paidValue: `${this.editedItem.paidValue}`,
                    openValue: `${this.editedItem.openValue}`,
                    bankAccount: `${this.editedItem.bankAccount}`,
                    paymentDate: `${this.editedItem.paymentDate}`,
                    observation: `${this.editedItem.observation}`,
                    category: `${this.editedItem.category}`,
                    costCenter1: `${this.editedItem.costCenter1}`,
                    costCenterAmount1: `${this.editedItem.costCenterAmount1}`,
                    costCenter2: `${this.editedItem.costCenter2}`,
                    costCenterAmount2: `${this.editedItem.costCenterAmount2}`,
                  },
                }
              )
              .then((response) => {
                if (response.status === 200 || response.status === 201) {
                  this.close()
                  this.showPost = !this.showPost
                  this.fetchInvoices()
                  setTimeout(() => {
                    this.showPost = !this.showPost
                  }, 1500)
                } else if (response.status === 429) {
                  alert(
                    "Erro ao Salvar" +
                      "O limite de solicitações foi excedido! 10.000 licenças foram lançadas esse mês! Entre em contato com o suporte para liberar mais espaço."
                  )
                } else {
                  alert(
                    "Erro ao Salvar" +
                      "Erro de servidor. Entre em contato com o suporte."
                  )
                }
              })
              .catch((error) => {
                if (error.response) {
                  this.showError = !this.showError
                  setTimeout(() => {
                    this.showError = !this.showError
                    window.location.reload()
                  }, 1500)
                }
              })
              const [day, month, year] = this.editedItem.dueDate.split("/")
              var date = new Date(year, month - 1, day)
              date.setDate(date.getDate() + this.installmentInterval)

              this.editedItem.dueDate = date.toLocaleDateString('pt-BR')
            }
          }
       })
      }
    },
    saveSupplier () {
      if (this.supplierExists === false) {
        axios.post(`https://sheetdb.io/api/v1/7j16zkhcavq27`, {
          data: {
            supplierId: `${this.editedItem.supplierId}`,
            supplierName: `${this.editedItem.supplierName}`,
          },
        })
      }
    },
    closeBatch () {
      this.selected = []
      this.batchDialog = false
      this.$nextTick(() => {
        this.batchItems = []
        this.objConditionals = {}
        this.editedItem = Object.assign({}, this.defaultItem)

        this.editedIndex = -1
        this.saving = false
      })
    },

    dMask(value) {
      value
      const day = [
        /[0-3]/,
        /[0-9]/,
        // value.toString().charAt(0) != '3' ? /[0-9]/ : /[0-1]/,
      ]
      const month = [
        /[0-1]/,
        /[0-9]/,
        // value.toString().charAt(3) === '1' ? /[0-2]/ : /[0-9]/,
      ]
      const year = [/[2]/, /[0]/, /[1-2]/, /[0-9]/]
      this.dateMask = [...day, "/", ...month, "/", ...year]
    },
    greaterId () {
      axios.get("https://sheetdb.io/api/v1/58i1u97yptnfw").then((r) => {
        this.editedItem.ID = r.data.length + 1
      })
    },
    greaterCompanyId () {
      axios.get("https://sheetdb.io/api/v1/7j16zkhcavq27").then((r) => {
        if (this.supplierExists === true) {
          const fr = r.data.filter(
            (item) => item.supplierName === this.editedItem.supplierName
          )
          this.editedItem.supplierId = fr[0].supplierId
          this.editedItem.supplierName = fr[0].supplierName
        }
      })
    },
  },
}
</script>

<style>
.grey-label .v-label {
  color: grey
}
.v-text-field input {
  height: 100% !important
}
.v-input input {
  max-height: 100% !important
}

.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease
}

.v-enter-from,
.v-leave-to {
  opacity: 0
}
</style>
